import React from 'react';
import { Link } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import { Box, Typography, Grid, Card, CardMedia, CardContent } from '@material-ui/core';

import berlin from './/images/berlin.jpg'
import poznan from './/images/poznan.jpg'
import milan from './/images/milan.jpg'

import uniqueLocData from './data/unique_locations_linktree.json'

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(4),
        backgroundColor: theme.palette.background.dark,
        color: theme.palette.text.primary,
        transition: 'background-color 0.5s ease, color 0.5s ease',
    },
    travelContent: {
        marginTop: theme.spacing(5),
        width: '100%',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        flexDirection: 'column',
    },
    travelCard: {
        maxWidth: 345,
        margin: theme.spacing(2),
        boxShadow: theme.shadows[5],
        transition: 'transform 0.3s, box-shadow 0.3s',
        cursor: 'pointer',
        '&:hover': {
            transform: 'scale(1.05)',
            boxShadow: theme.shadows[10],
        },
    },
    media: {
        height: 140,
    },
}));

const ExtraContent = () => {
    const classes = useStyles();


    return (
        <Box className={classes.root}>
            <Box className={classes.travelContent}>
                <Typography color='secondary' variant="h5" gutterBottom>
                    Our 2024 Travel Highlights
                </Typography>
                <Grid container spacing={3} justifyContent="center">
                    {uniqueLocData.map((trip, index) => (
                        <Grid item xs={12} sm={6} md={4} key={index}>
                            <Link to={`/travelplans?title=${encodeURIComponent(trip.title)}`}>
                                <Card className={classes.travelCard}>
                                    <CardMedia
                                        component="img"
                                        className={classes.media}
                                        title={trip.title}
                                        src={trip.image ? "https://picsum.photos/200/300" : berlin}
                                    />
                                    <CardContent>
                                        <Typography variant="h6" component="h2">
                                            {trip.title}
                                        </Typography>
                                        <Typography variant="body2" color="textSecondary" component="p">
                                            {trip.description}
                                        </Typography>
                                    </CardContent>
                                </Card>
                            </Link>
                        </Grid>
                    ))}
                </Grid>
            </Box>
        </Box>
    );
};

export default ExtraContent;